<template>
  <div class="blogs">
    <i class="el-icon-back allback" @click="$emit('back')"></i>
    <div class="blog_box">
      <div class="item" v-for="(item,index) in blogs" :key="index">
        <div class="title">
          <span class="cursorp" @click="goblogx(item.id)">
            <span class="stick" v-if="item.isStick==1">[置顶]</span>
            {{item.title}}
          </span>
          <div class="time">{{item.time | time}}</div>
        </div>
        <div class="tags">
          <i>分类：</i>
          <span class="tag" v-for="(tag,i) in tags(item.classify)" :key="i">{{tag}}</span>
          <i class="pl">
            评论：
            <span>180</span>
          </i>
          <i class="pl">
            浏览：
            <span>{{item.browse}}</span>
          </i>
        </div>
        <div class="ctitle">{{item.ctitle}}</div>
      </div>
      <!-- 分页 -->
      <el-pagination
        :current-page="page"
        :total="total"
        :page-size="pagenum"
        :pager-count="5"
        @current-change="pagechange"
        class="logfenye"
        layout="prev, pager, next"
      ></el-pagination>
      <!-- 添加博客按钮 -->
      <div v-if="isLogin" @click="openbianji" class="addblogbtn cursorp">写</div>
    </div>
    <!-- 编辑器 -->
    <bianjiq
      v-if="bianjiFlage"
      class="bianjiqi"
      :quxiao="ruxiao"
      :queding="queding"
      :startext="content"
    />
    <!-- 标题确认输入框 -->
    <div class="titlebox" v-if="titleboxflage">
      <h3 style="text-align:center;">博客标题确认</h3>
      <el-form
        ref="addblogform"
        :model="formdata"
        prop
        :rules="rules"
        label-width="80px"
        class="FORM"
      >
        <el-form-item label="标题：" prop="title">
          <el-input placeholder="文章标题" v-model="formdata.title"></el-input>
        </el-form-item>
        <el-form-item label="标签：" prop="classify">
          <el-input placeholder="标签之间空格分隔例如：tag1 tag2" v-model="formdata.classify"></el-input>
        </el-form-item>
        <el-form-item label="简述：" prop="ctitle">
          <el-input placeholder="文章简述" v-model="formdata.ctitle"></el-input>
        </el-form-item>
        <el-form-item label="是否置顶:" prop="isStick">
          <el-switch v-model="formdata.isStick"></el-switch>
        </el-form-item>
      </el-form>
      <el-button class="titlebtn cursorp" @click="blogsadd">确 定</el-button>
      <el-button class="titlebtn cursorp" @click="titleboxflage = false">取 消</el-button>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
// 引入编辑器
import bianjiq from "../../components/wangeditor";
export default {
  components: {
    bianjiq //编辑器
  },
  computed: {
    ...mapState([
      "allnavs", //导航数据
      "isLogin", //是否登录
      "USER_ID", //用户ID
      "isPC" //是否是PC端
    ])
  },
  data() {
    return {
      blogs: [
        {
          id: 1,
          title: "UEditor百度编辑器在html代码模式中无法更新内容",
          time: "1583301706251",
          classify: "UEditor 编辑器",
          browse: 0,
          ctitle:
            "使用UEdirot百度编辑器发现一个bug，就是切换到源代码模式发现，无法保存更新内容，保存后发现内容还是老的，可以通过一下办法解决使用UEdirot百度编辑器发现一个bug，就是切换到源代码模式发现，无法保存更新内容，保存后发现内容还是老的，可以通过一下办法解决",
          user_id: 5,
          isStick: "0"
        },
        {
          id: 1,
          title: "UEditor百度编辑器在html代码模式中无法更新内容",
          time: "1583301706251",
          classify: "UEditor 编辑器",
          browse: 0,
          ctitle:
            "使用UEdirot百度编辑器发现一个bug，就是切换到源代码模式发现，无法保存更新内容，保存后发现内容还是老的，可以通过一下办法解决使用UEdirot百度编辑器发现一个bug，就是切换到源代码模式发现，无法保存更新内容，保存后发现内容还是老的，可以通过一下办法解决",
          user_id: 5,
          isStick: "0"
        }
      ],
      page: 1,
      pagenum: 12,
      total: 0,
      // 编辑器
      bianjiFlage: false,
      content: `<h1 style="text-align: center;">这是一个标题哟~</h1><p><br></p>`,
      titleboxflage: false,
      formdata: {
        title: "",
        classify: "",
        ctitle: "",
        isStick: false,
        content: ""
      },
      rules: {
        title: [
          { required: true, message: "请输入标题", trigger: "change" },
          {
            min: 3,
            max: 16,
            message: "长度在 3 到 16 个字符",
            trigger: "change"
          }
        ],
        classify: [
          { required: true, message: "请输入标签", trigger: "change" },
          {
            min: 1,
            max: 16,
            message: "长度在 1 到 16 个字符",
            trigger: "change"
          }
        ],
        ctitle: [{ required: true, message: "请输简述", trigger: "change" }]
      }
    };
  },
  methods: {
    // 获取博客列表
    async getblogs() {
      let { data: res } = await this.$http.get("/blogs/" + this.USER_ID, {
        params: {
          page: this.page,
          pagenum: this.pagenum
        }
      });
      this.blogs = res.data;
      this.total = res.total;
    },
    // 返回数据
    tags(classify) {
      return classify.split(" ");
    },
    // 页码改变
    pagechange(page) {
      this.page = page;
      // 重新获取数据
      this.getblogs();
    },
    // 取消
    ruxiao() {
      this.titleboxflage = false;
      this.bianjiFlage = false;
    },
    queding(content) {
      this.formdata.content = content;
      this.titleboxflage = true;
    },
    blogsadd() {
      this.$refs["addblogform"].validate(async valid => {
        if (valid) {
          let { data: res } = await this.$http.post("/blogs", this.formdata);
          if (res.ok == 0)
            return this.$notify.error({
              title: "添加失败",
              customClass: "alter"
            });
          this.$notify.success({
            title: "添加成功~",
            customClass: "alter"
          });
          // 从新获取数据
          this.getblogs();
          this.$refs["addblogform"].resetFields();
          // 关闭框
          this.titleboxflage = false;
          this.bianjiFlage = false;
        }
      });
    },
    // 打开编辑器
    openbianji() {
      //  判断
      if (!this.isPC)
        return this.$notify.error({
          title: "请在PC端编辑内容哟",
          customClass: "alter"
        });
      this.bianjiFlage = true;
    },
    // 去详情页面
    goblogx(id) {
      this.$router.push({ path: `/blogx/${id}` });
    }
  },
  created() {},
  watch: {
    allnavs: {
      handler(allnavs) {
        // 判断是否允许访问
        if (allnavs.navs && allnavs.navs.split(",")[1] != 1) {
          return this.$router.push("/");
        }
      },
      // 深度监听
      deep: true
    },
    USER_ID: {
      handler(userID) {
        // 获取数据
        if (userID != "") this.getblogs();
      },
      immediate: true
    }
  },
  filters: {
    time(time) {
      return new Date(Number(time)).toLocaleDateString();
    }
  }
};
</script>

<style>
.blogs .blog_box {
  line-height: 40px;
  padding-bottom: 70px;
  position: relative;
  min-height: 100%;
}
.blogs .blog_box .item {
  background-image: linear-gradient(190deg, #778899 15%, transparent 45%);
  max-width: 1200px;
  max-height: 195px;
  margin: 40px auto;
  padding: 25px;
  text-overflow: ellipsis;
  border-bottom: 1px solid #222428;
}
.item .time {
  float: right;
  color: #b9b7c7;
  font-size: 12px;
}
.item .title > span {
  max-width: 50%;
  color: #006699;
  font-weight: bold;
  white-space: nowrap;
  float: left;
  border-bottom: 1px dashed transparent;
}
.item .tags span.tag {
  padding: 2px;
  background-color: #dff0fb;
  display: inline-block;
  margin: 0 5px;
  vertical-align: middle;
  font-size: 13px;
  line-height: 13px;
  color: #8f677d;
}
.item .title span:hover {
  border-color: #006699;
}
.item .ctitle {
  line-height: 30px;
  color: #dff0fb;
  font-size: 14px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
i.pl {
  font-size: 12px;
  padding-left: 15px;
}
.logfenye {
  background-color: transparent;
  margin: 0 auto;
  position: absolute;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%)
}
.logfenye * {
  background-color: transparent !important;
}
.logfenye .number {
  color: white;
}
.logfenye .active {
  color: aqua !important;
}
/* 添加博客按钮 */
.addblogbtn {
  position: absolute;
  bottom: 0px;
  right: 20px;
  padding: 15px;
  line-height: 30px;
  font-size: 35px;
  font-family: "Microsoft Yahei";
  font-weight: bold;
  background: linear-gradient(160deg, #778899 20%, transparent 80%);
  -webkit-background-clip: text;
  color: transparent;
}
/* 置顶样式 */
.stick {
  color: #ff3f3f !important;
  padding: 0px 5px;
}
/* 添加博客标题确认框 */
.titlebox {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); /*2d移动*/
  width: 50%;
  height: 400px;
  border: 1px solid #778899;
  animation: ru 0.6s;
  padding: 40px;
  background-image: linear-gradient(160deg, #778899 15%, transparent 45%);
}
.titlebox .el-form-item__label {
  color: white !important;
}
@keyframes ru {
  0% {
    top: 40%;
    opacity: 0;
  }
  100% {
    top: 50%;
    opacity: 1;
  }
}
.titlebtn {
  background-color: transparent !important;
}
</style>